
@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@300&display=swap');
.App {
  text-align: center;
  font-family: 'Quicksand', sans-serif;
}

body {
  padding: 0;
  margin: 0;
}
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #6B9B8A; 
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: rgb(255, 255, 255);
  border-radius: 5px;

}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: rgb(228, 228, 228);

}

#fileID {
  font-size: 15px;
  text-align: center;
  text-align-last: center;

  margin-bottom: 5px;
  margin-left: 0;
  margin-right: 0;
  width: 180px;
  font-family: 'Quicksand', sans-serif;
}
@keyframes focused {
  from {
    border-radius: 8px;
  }
  to {
    border-radius: 5px;
  }
}
@keyframes unfocused {
  from {
    border-radius: 5px;
  }
  to {
    border-radius: 8px;
  }
}
#fileID::-webkit-file-upload-button{
  border-radius: 5px;
  animation-name: unfocused;
  animation-timing-function: linear;
  animation-duration: 150ms;
  height: 25px;
  border-radius: 8px;
  border:none;
  background-color: white;
  width:55px;
  font-family: 'Quicksand', sans-serif;
}
#fileID::-webkit-file-upload-button:hover {
  cursor: pointer;
  animation-name: focused;
  animation-timing-function: linear;
  animation-duration: 150ms;
  border-radius: 5px;
}



.Description {
  margin: 5px;
  font-size: 15px;
  text-align: center;
  border: none;
  height: 25px;
  border-radius: 8px;
  width: 180px;
  font-family: 'Quicksand', sans-serif;
}
.Description:focus {
  outline: none;
}

button {
  margin: 5px;
  font-size: 15px;
  border-radius: 5px;
  animation-name: unfocused;
  animation-timing-function: linear;
  animation-duration: 150ms;
  border-radius: 8px;
  border:none;
  background-color: white;

  font-family: 'Quicksand', sans-serif;

  width: 180px;
  height: 25px;

  cursor: pointer;
}

button:hover {
  border-radius: 8px;
  animation-name: focused;
  animation-timing-function: linear;
  animation-duration: 150ms;
  border-radius: 5px;
}

.PostsButton {
  position: absolute;
  top: 0;
  left: 0;
  margin-left: 10px;
  margin-top: 6px;
  color: black;
  text-decoration: none;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #6B9B8A;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

h1 {
  font-size: 6vw;
  margin: 0;
}

.Button {
  position: fixed;
  width: 120px;
  height: 30px;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
